import dsbridge from "dsbridge";
const module_names = new Set([]);
const patch = {}

function isFunction(functionToCheck) {
 return functionToCheck && {}.toString.call(functionToCheck) === '[object Function]';
}

function isObject(val) {
    if (val === null) { return false;}
    return ( (typeof val === 'function') || (typeof val === 'object') );
}

function isString(x) {
  return Object.prototype.toString.call(x) === "[object String]"
}

function isHybrid(){
  return window && window._dswk===true;
}
let xengine = {
  patch   : patch,
  platfrom: platform(),
  hybrid  : isHybrid(),
  bridge  :dsbridge,
  use     :use
};

function use(ns,funcs){
    if(module_names.has(ns)){
      throw(ns+ ',,,xengine.use("'+ns+'") ;')
    }
    module_names.add(ns)
    console.log(ns+ ',')

    let _call = function(funcname,args){
      if (args.hasOwnProperty('__event__')){
         let eventcb = args['__event__'];
         if(!isFunction(eventcb)) throw('__event__ ');
         args['__event__']  = ns+funcname+'__event__'
          dsBridge.register(args['__event__'], (res) => {
              eventcb(res);
          })
      }
      else  if (args.hasOwnProperty('$xevent')){
          alert("$xevent,__event__");
          return;
      }      // TODO, reject?

      let p =  new Promise((resolve, reject)=>{
          dsBridge.call(ns+"."+funcname, args, function (res) {
            resolve(res)
          })
      });
      return p;
    };

    return funcs.reduce((acc,cur,i)=>{
      if(isObject(cur)){
        acc[cur.name] = args => _call(cur.name,{...cur.default_args,...args});
      }else if(isString(cur)){
        acc[cur] = args => _call(cur,args);
      }else{
        throw(" string  {name:xxx, default_args:{...}}")
      }
      return acc;
    },{})
}

Object.defineProperty(xengine, "bridge", {
  get(){
    return dsbridge;
  },
  set: function () {
    throw('dsbridge')
  }
});

function platform() {
     var ua = navigator.userAgent,
     isWindowsPhone = /(?:Windows Phone)/.test(ua),
     isSymbian = /(?:SymbianOS)/.test(ua) || isWindowsPhone, 
     isAndroid = /(?:Android)/.test(ua), 
     isFireFox = /(?:Firefox)/.test(ua), 
     isChrome = /(?:Chrome|CriOS)/.test(ua),
     isTablet = /(?:iPad|PlayBook)/.test(ua) || (isAndroid && !/(?:Mobile)/.test(ua)) || (isFireFox && /(?:Tablet)/.test(ua)),
     isPhone = /(?:iPhone)/.test(ua) && !isTablet,
     isPc = !isPhone && !isAndroid && !isSymbian;
     return {
          isTablet: isTablet,
          isPhone: isPhone,
          isAndroid : isAndroid,
          isPc : isPc
     };
};


